module.exports = [{"name":"Avatar","description":"Image avatars.","props":{"src":{"type":{"name":"string"},"required":true,"description":"Path to the image file"},"alt":{"type":{"name":"string"},"required":false,"description":"Alternate text for the image","defaultValue":{"value":"''","computed":false}},"outlined":{"type":{"name":"bool"},"required":false,"description":"Show or hide image outline","defaultValue":{"value":"false","computed":false}},"size":{"type":{"name":"enum","value":[{"value":"'small'","computed":false},{"value":"'default'","computed":false}]},"required":false,"description":"Pre-set avatar size","defaultValue":{"value":"'default'","computed":false}},"round":{"type":{"name":"bool"},"required":false,"description":"Set to true to make the image round","defaultValue":{"value":"false","computed":false}}},"code":"import React from 'react';\nimport PropTypes from 'prop-types';\nimport styled, { ThemeProvider } from 'styled-components';\n\nconst getSize = ({ theme: { size } }) => size;\nconst calcSize = (percent) => ({ theme: { size } }) => size + size * percent;\nconst sizes = {\n  small: 3.75,\n  default: 6.25,\n};\n\nconst Wrapper = styled.div`\n  background-color: white;\n  width: ${calcSize(0.2)}em;\n  height: ${calcSize(0.2)}em;\n  border-radius: ${({ theme: { size } }) => size * 0.1}em;\n`;\n\nconst Img = styled.img`\n  height: ${getSize}em;\n  width: ${getSize}em;\n  border-radius: ${({ theme: { size, round } }) =>\n    round ? '50%' : `${size * 0.1}em`};\n  margin: ${({ theme: { size, outlined, round } }) =>\n    outlined && !round ? size * 0.1 : 0}em;\n`;\n\n/** Image avatars. */\nexport default function Avatar({ src, alt, outlined, size, round }) {\n  const theme = {\n    size: sizes[size],\n    outlined,\n    round,\n  };\n\n  return (\n    <ThemeProvider theme={theme}>\n      {outlined && !round ? (\n        <Wrapper>\n          <Img src={src} alt={alt} />\n        </Wrapper>\n      ) : (\n        <Img src={src} alt={alt} />\n      )}\n    </ThemeProvider>\n  );\n}\n\nAvatar.defaultProps = {\n  alt: '',\n  outlined: false,\n  size: 'default',\n  round: false,\n};\n\nAvatar.propTypes = {\n  /** Path to the image file */\n  src: PropTypes.string.isRequired,\n\n  /** Alternate text for the image */\n  alt: PropTypes.string,\n\n  /** Show or hide image outline */\n  outlined: PropTypes.bool,\n\n  /** Pre-set avatar size */\n  size: PropTypes.oneOf(['small', 'default']),\n\n  /** Set to true to make the image round */\n  round: PropTypes.bool,\n};\n","examples":[{"name":"ExampleBasic","description":"Default avatar","code":"import React from 'react';\nimport Avatar from 'alys/Avatar';\n\nimport img from '../../docs/assets/alys.jpg';\n\n/** Default avatar */\nexport default function ExampleBasic() {\n  return <Avatar src={img} />;\n}\n"},{"name":"ExampleOutlined","description":"Small size with outline","code":"import React from 'react';\nimport Avatar from 'alys/Avatar';\n\nimport img from '../../docs/assets/alys.jpg';\n\n/** Small size with outline */\nexport default function ExampleOutlined() {\n  return <Avatar src={img} size=\"small\" outlined />;\n}\n"},{"name":"ExampleSmallRound","description":"Small and rounded avatar","code":"import React from 'react';\nimport Avatar from 'alys/Avatar';\n\nimport img from '../../docs/assets/alys.jpg';\n\n/** Small and rounded avatar */\nexport default function ExampleSmallRound() {\n  return <Avatar src={img} size=\"small\" round />;\n}\n"},{"name":"ExampleSmallSize","description":"Small avatar","code":"import React from 'react';\nimport Avatar from 'alys/Avatar';\n\nimport img from '../../docs/assets/alys.jpg';\n\n/** Small avatar */\nexport default function ExampleSmallSize() {\n  return <Avatar src={img} size=\"small\" />;\n}\n"}]},{"name":"EyeIcon","description":"SVG eye icon.","props":{"fill":{"type":{"name":"union","value":[{"name":"string"},{"name":"custom","raw":"eyeIconColorEnum"}]},"required":false,"description":"Fill color","defaultValue":{"value":"'default'","computed":false}},"width":{"type":{"name":"custom","raw":"stringOrNumberProp"},"required":false,"description":"Icon width","defaultValue":{"value":"'1em'","computed":false}},"height":{"type":{"name":"custom","raw":"stringOrNumberProp"},"required":false,"description":"Icon height","defaultValue":{"value":"'1em'","computed":false}}},"code":"import React from 'react';\nimport PropTypes from 'prop-types';\n\nimport { styleLengths } from '@utils/string-utils';\n\n/** SVG eye icon. */\nexport default function EyeIcon({ fill, width, height }) {\n  return (\n    <svg\n      xmlns=\"http://www.w3.org/2000/svg\"\n      width={styleLengths(width)}\n      height={styleLengths(height)}\n      viewBox=\"0 0 22 22\"\n    >\n      <g\n        transform=\"matrix(.02146 0 0 .02146 1 1)\"\n        fill={fill === 'default' ? '#4d4d4d' : fill}\n      >\n        <path d=\"m466.07 161.53c-205.6 0-382.8 121.2-464.2 296.1-2.5 5.3-2.5 11.5 0 16.9 81.4 174.9 258.6 296.1 464.2 296.1 205.6 0 382.8-121.2 464.2-296.1 2.5-5.3 2.5-11.5 0-16.9-81.4-174.9-258.6-296.1-464.2-296.1m0 514.7c-116.1 0-210.1-94.1-210.1-210.1 0-116.1 94.1-210.1 210.1-210.1 116.1 0 210.1 94.1 210.1 210.1 0 116-94.1 210.1-210.1 210.1\" />\n        <circle cx=\"466.08\" cy=\"466.02\" r=\"134.5\" />\n      </g>\n    </svg>\n  );\n}\n\nconst FILL_DEFAULT = 'default';\nconst DEFAULT_LENGTH = '1em';\nconst stringOrNumberProp = PropTypes.oneOfType([\n  PropTypes.string,\n  PropTypes.number,\n]);\nexport const eyeIconColorEnum = PropTypes.oneOf([FILL_DEFAULT]);\n\nEyeIcon.defaultProps = {\n  fill: FILL_DEFAULT,\n  width: DEFAULT_LENGTH,\n  height: DEFAULT_LENGTH,\n};\n\nEyeIcon.propTypes = {\n  /** Fill color */\n  fill: PropTypes.oneOfType([PropTypes.string, eyeIconColorEnum]),\n\n  /** Icon width */\n  width: stringOrNumberProp,\n\n  /** Icon height */\n  height: stringOrNumberProp,\n};\n","examples":[{"name":"Example4em","description":"4em x 4em","code":"import React from 'react';\nimport EyeIcon from 'alys/EyeIcon';\n\n/** 4em x 4em */\nexport default function Example4em() {\n  return <EyeIcon width=\"4em\" height=\"4em\" />;\n}\n"},{"name":"Example64x64","description":"64px x 64px","code":"import React from 'react';\nimport EyeIcon from 'alys/EyeIcon';\n\n/** 64px x 64px */\nexport default function Example64x64() {\n  return <EyeIcon width={64} height={64} />;\n}\n"},{"name":"ExampleBasic","description":"Basic eye icon","code":"import React from 'react';\nimport EyeIcon from 'alys/EyeIcon';\n\n/** Basic eye icon */\nexport default function ExampleBasic() {\n  return <EyeIcon />;\n}\n"},{"name":"ExampleRed","description":"Filled with \"red\"","code":"import React from 'react';\nimport EyeIcon from 'alys/EyeIcon';\n\n/** Filled with \"red\" */\nexport default function ExampleRed() {\n  return <EyeIcon fill=\"red\" />;\n}\n"}]},{"name":"ProgressBar","description":"A custom progress bar.","props":{"percent":{"type":{"name":"number"},"required":true,"description":"Percent of progress completed"},"width":{"type":{"name":"number"},"required":true,"description":"Bar width"},"height":{"type":{"name":"number"},"required":false,"description":"Bar height","defaultValue":{"value":"5","computed":false}}},"code":"import React from 'react';\nimport PropTypes from 'prop-types';\nimport styled from 'styled-components';\n\nimport { getWidthAsPercentOfTotalWidth } from '@utils/percent-utils';\nimport { styleLengths } from '@utils/string-utils';\n\nconst ProgressBarWrapper = styled.div`\n  border: solid 1px lightgray;\n  width: ${({ width }) => styleLengths(width)};\n`;\n\nconst ProgressBarValue = styled.div`\n  width: ${({ percent, width }) =>\n    styleLengths(getWidthAsPercentOfTotalWidth(percent, width))};\n  height: ${({ height }) => styleLengths(height)};\n  background-color: ${({ percent }) => {\n    if (percent === 100) return 'green';\n\n    return percent > 50 ? 'lightgreen' : 'red';\n  }};\n`;\n\n/** A custom progress bar. */\nexport default function ProgressBar({ width, height, percent }) {\n  return (\n    <ProgressBarWrapper width={width}>\n      <ProgressBarValue width={width} height={height} percent={percent} />\n    </ProgressBarWrapper>\n  );\n}\n\nProgressBar.defaultProps = {\n  height: 5,\n};\n\nProgressBar.propTypes = {\n  /** Percent of progress completed */\n  percent: PropTypes.number.isRequired,\n\n  /** Bar width */\n  width: PropTypes.number.isRequired,\n\n  /** Bar height */\n  height: PropTypes.number,\n};\n","examples":[{"name":"Example100Percent","description":"100% progress","code":"import React from 'react';\nimport ProgressBar from 'alys/ProgressBar';\n\n/** 100% progress */\nexport default function Example100Percent() {\n  return <ProgressBar percent={100} width={150} />;\n}\n"},{"name":"Example10Percent","description":"10% progress","code":"import React from 'react';\nimport ProgressBar from 'alys/ProgressBar';\n\n/** 10% progress */\nexport default function Example10Percent() {\n  return <ProgressBar percent={10} width={150} />;\n}\n"},{"name":"Example20pxHeight","description":"20px height and 100% progress","code":"import React from 'react';\nimport ProgressBar from 'alys/ProgressBar';\n\n/** 20px height and 100% progress */\nexport default function Example20pxHeight() {\n  return <ProgressBar percent={100} width={150} height={20} />;\n}\n"},{"name":"Example70Percent","description":"70% progress","code":"import React from 'react';\nimport ProgressBar from 'alys/ProgressBar';\n\n/** 70% progress */\nexport default function Example70Percent() {\n  return <ProgressBar percent={70} width={150} />;\n}\n"}]}];
